@use './../../../styles/imports' as *;
@use 'sass:map';

$this-block: 'accordion';

.#{$this-block} {
  display: flex;
  flex-direction: column;
  gap: var(--border-width);
  &-item {
    border-radius: map.get($border-radius, 'lg');
    &-active {
      .#{$this-block}-header {
        border-radius: map.get($border-radius, 'lg') map.get($border-radius, 'lg') 0 0;
      }
      .#{$this-block}-collapse {
        border-radius: 0 0 map.get($border-radius, 'lg') map.get($border-radius, 'lg');
      }
    }
  }
  &-header {
    border-radius: map.get($border-radius, 'lg');
    border: var(--border-width) solid var(--color-gray-5);
    display: flex;
    margin: 0;
  }
  &-collapse {
    border-radius: map.get($border-radius, 'lg');
    border: var(--border-width) solid var(--color-gray-5);
    display: none;
    padding: map.get($gap-sizes, 5);
    transform: translate(0, px-to-rem(-1px));
  }
  &-toggle {
    display: none;
  }
  &-button {
    background-color: transparent;
    border: 0;
    color: inherit;
    cursor: pointer;
    padding: 0;
    padding: map.get($gap-sizes, 5) map.get($gap-sizes, 9) map.get($gap-sizes, 5)
      map.get($gap-sizes, 5);
    position: relative;
    text-align: left;
    vertical-align: top;
    width: 100%;
    span {
      font-size: 1.5em;
      position: absolute;
      right: map.get($gap-sizes, 5);
      top: 50%;
      transform: translate(0, -50%);
      &.#{$this-block}-icon-open {
        display: block;
      }
      &.#{$this-block}-icon-close {
        display: none;
      }
    }
    &[aria-expanded='true'] {
      background-color: var(--color-active-state);
      span {
        &.#{$this-block}-icon-open {
          display: none;
        }
        &.#{$this-block}-icon-close {
          display: block;
        }
      }
    }
  }
}
